.header-nav {
  background-color: #f6f4ef;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

.link {
  text-decoration: none;
  color: #395a13;
}

.header-logo {
  margin: 10px 0 10px 15px;
  width: 140px;
  height: 80px;
}

#nav {
  margin-left: 2em;
  display: flex;
  list-style-type: none;
  margin: 10px;
  padding: 0;
  align-items: center;

}

#nav li,
#contactUs li {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  width: max-content;
  text-align: center;
  padding: 0.5em 1em 0.5em 1em;
  margin: 0 0.2em 0 0.2em;
  position: relative;
}


#contactUs {
  align-items: center;
  display: flex;
  list-style-type: none;
  margin: 10px;
  padding: 0;
  justify-content: center;
}

@media (max-width:820px) {
  #contactUs {
    display: none;
  }
}

#nav li:hover {
  height: 40px;

}

#contactUs li:hover {
  height: 40px;

}


#nav li::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #395a13;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}

#contactUs>li::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #395a13;
  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}

#contactUs>li:hover::after {
  transform: scaleX(1);
}

#contactUs:hover::after {
  transform: scaleX(1);
}

#nav>li:hover::after {
  transform: scaleX(1);
}

#nav:hover::after {
  transform: scaleX(1);
}